@import url(https://fonts.googleapis.com/css2?family=MedievalSharp&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
.fantasyHeader{
    margin: auto;
    width: 100vw;
    max-width: 62.5rem;
    position: relative;
}

.fantasyHeader__pic{
    width: 100vw;
    max-width: 62.5rem;
}

.fantasyHeader__title{
    position: absolute;
    top: 5rem;
    left: 5rem;
    font-size: 5rem;
    font-family: 'MedievalSharp', cursive;
    color: rgb(177, 138, 80);
    text-shadow: -1px -1px 0 rgb(54, 14, 14), 1px -1px 0 rgb(54, 14, 14), -1px 1px 0 rgb(54, 14, 14), 1px 1px 0 rgb(54, 14, 14);
}

@media screen and (max-width: 62.5rem ) {
    .fantasyHeader__title{
        top: 4rem;
        left: 4rem;
        font-size: 3rem;
    }
}
.scifiHeader{
    margin: auto;
    width: 100vw;
    max-width: 62.5rem;
    position: relative;

}

.scifiHeader__pic{
    width: 100vw;
    max-width: 62.5rem;

}

.scifiHeader__title{
    position: absolute;
    top: 10rem;
    right: 10rem;
    font-size: 5rem;
    font-family: 'Patrick Hand', cursive;
    color: rgb(253, 193, 253);
    font-weight: 100;
}
.scifiHeader__title-overlay{
    position: absolute;
    top: 10rem;
    right: 10rem;
    font-size: 5rem;
    font-family: 'Patrick Hand', cursive;
    color: rgb(255, 1, 255);
    -webkit-filter: blur(15px);
            filter: blur(15px);
    
}

@media screen and (max-width: 62.5rem ) {

    .scifiHeader__title{
        left: 10rem;
        font-size: 4rem;
        font-weight: 100;
    }

    .scifiHeader__title-overlay{
        left: 10rem;
        font-size: 4rem;
        -webkit-filter: blur(15px);
                filter: blur(15px);
        
    }
}
.fantasyLogin{
    display: block;
    margin-top: 3rem;
    margin-left: 14rem;
    width: 48rem;
    text-align: right;
}

@media screen and (max-width: 62.5rem ) {
    .fantasyLogin{
        display: block;
        margin: auto;
        width: 16rem;
        text-align: center;
    }
}


@media screen and (max-width: 62.5rem ) {
    .fantasyLoginForm{
        margin-bottom: 1rem;
    }
}



.scifiLogin{
    padding: 1rem 0 3rem 0;
    width: 49.5%;
    background-color: #b218b1;
    text-align: center;
    height: 22rem
}
.fantasy__btn{
    width: 10rem;
    height: 1.5rem;
    border-radius: 0;
    background: rgb(54, 14, 14);
    border: 2px solid rgb(177, 138, 80);
    color: rgb(177, 138, 80);
    font-family: 'MedievalSharp', cursive;
    cursor: pointer;
    transition: 300ms;
}

.fantasy__btn:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-filter: brightness(140%);
            filter: brightness(140%);
    transition: 300ms;
}

.fantasy__btn-short{
    width: 5rem;
    height: 2rem;
}


.fantasy__btn-big{
    height: 5rem;
}

.fantasy__btn-exit{
    height: 2rem;
    width: 2rem;
    border-radius: 2rem;
    position: absolute;
    top: -1rem;
    right: -1rem;

}
.scifi__btn{
    width: 10rem;
    height: 2rem;
    border-radius: 0;
    background: rgba(255, 255, 255, 0.5);
    border: 0;
    border-bottom: 2px solid rgba(0, 0, 0, 0.3);
    border-top: 2px solid rgba(255, 255, 255, 0.7);
    border-radius: 1rem;
    color: rgb(255, 255, 255);
    font-family: 'Patrick Hand', cursive;
    cursor: pointer;
    transition: 300ms;
}

.scifi__btn:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-filter: brightness(140%);
            filter: brightness(140%);
    transition: 300ms;
}

.scifi__btn-short{
    width: 5rem;
    height: 2rem;
}


.scifi__btn-big{
    height: 5rem;
}

.scifi__btn-exit{
    height: 2rem;
    width: 2rem;
    border-radius: 2rem;
    position: absolute;
    top: -1rem;
    right: -1rem;

}
.fantasy__textInput{
    width: 14rem;
    margin: 0.5rem 1rem 1rem 1rem;
    padding: 0 1rem;
    height: 2rem;
    text-align: center;
    background: rgb(177, 138, 80);
    color: rgb(54, 14, 14);
    border: 2px rgb(54, 14, 14) solid;
}

@media screen and (max-width: 62.5rem ) {

    .fantasy__textInput{
        display: block;
    }
    
}

.fantasy__textInput:focus{
    -webkit-filter: brightness(130%);
            filter: brightness(130%);
    outline: 0;
}

.fantasy__textInput-invalid{
    border: 3px solid red;
}

.fantasy__label{
    margin: 1rem;
    color: rgb(177, 138, 80);
    font-family: 'MedievalSharp', cursive;
}

@media screen and (max-width: 62.5rem ) {

    .fantasy__label{
        display: block;
    }
    
}


.scifi__textInput{
    width: 12rem;
    margin: 0.5rem auto 1rem auto;
    padding: 0 1rem;
    height: 2rem;
    text-align: center;
    background: rgba(0, 0, 0, 0.3);
    color: rgb(255, 255, 255);
    border-radius: 1rem;
    border: 0;
    border-top: 2px solid rgba(0, 0, 0, 0.3);
    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
    display: block;

}

@media screen and (max-width: 62.5rem ) {

    .scifi__textInput{
        display: block;
    }
    
}

.scifi__textInput:focus{
    -webkit-filter: brightness(130%);
            filter: brightness(130%);
    outline: 0;
}

.scifi__textInput-invalid{ 
box-shadow: 0px 0px 16px 10px #FF0000;

}

.scifi__label{
    display: block;
    margin-top: 1rem;
    color: rgb(255, 255, 255);
    font-family: 'Patrick Hand', cursive;
}



.fantasy__reg{
    display: block;
    margin-top: -4.4rem;
}

@media screen and (max-width: 62.5rem ) {
    .fantasy__reg{
        margin: auto;
        margin-top: 2rem;
        width: 10rem;
    }
}

.fantasy__reg_wrapper{
    position: fixed;
    display: block;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}


.fantasy__reg_formBox{
    position: absolute;
    top: 50vh;
    left: 50vw;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: right;
    padding: 3rem 1rem;
    background: rgb(54, 14, 14);
    width: 27rem;
    height: 20rem;
    border: 2px solid rgb(177, 138, 80); 
box-shadow: 0px 0px 20rem 10rem #000000; 
}

@media screen and (max-width: 62.5rem ) {
    .fantasy__reg_formBox{
        width: 25rem;
        text-align: center;
        height: 25rem;
        padding: 3rem 4.5rem;

    }
}


.fantasy__reg_btnBox{
    margin-top: 2rem;
    text-align: center;
}


.scifi__reg{
    position: absolute;
    top: 0;
    left: 51%;
    width: 49%;
    background-color: #3ab9c7;
    padding: 1rem 0 3rem 0;
    height: 22rem;
    text-align: center;
}


